
import util from '@/libs/util'
import common from '@/libs/common.mixin'
import globals from '@/libs/globals.mixin'
import Card from 'vux/src/components/card/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'

import Tag from 'c%/Tag.vue'
export default{
mixins: [common,globals],
components: {
Card,
Tag,
Flexbox,
FlexboxItem
},
data() {
return {
data_uri: 'student_exam_scores/query_exam',
}
},
mounted() {

},
methods: {
getExamtype(item) {
return this.$filter('dict_title')(item.exam_type_did,'exam_type')
},
getSubjects(item) {
let subjects = []
item.exam_subject_dids.forEach(did=>{
subjects.push(this.$filter('dict_title')(did,'exam_subject'))
})
return subjects.join(',')
},
showDetail(item) {
this.$router.push({path:'./exam/'+item.se_id})
}
}
}



